            },
            options: {
              axisX: {
                showGrid: false,
              },
              low: 0,
              high: 1000,
              chartPadding: {
                top: 0,
                right: 5,
                bottom: 0,
                left: 0,
              },
            },
            responsiveOptions: [
              ['screen and (max-width: 640px)', {
                seriesBarDistance: 5,
                axisX: {
                  labelInterpolationFnc: function (value) {
                    return value[0]
                  },
                },
              }],
            ],
          },
          headers: [
            {
              sortable: false,
              text: 'Nom',
              value: 'nom',
            },
            {
              sortable: false,
              text: 'Prenom',
              value: 'prenom',
            },
            {
              sortable: false,
              text: 'Email',
              value: 'email',
            },
            {
              sortable: false,
              text: 'Contact',
              value: 'contact',
            },
            {
              sortable: false,
              text: 'Ville',
              value: 'ville',
            },
          ],
          items: [],
          tabs: 0,
          tasks: {
            0: [
              {
                text: 'Sign contract for "What are conference organizers afraid of?"',
                montant: 200000,
                value: true,
              },
              {
