            0: false,
            1: false,
            2: false,
          },
        }
      },
      created(){
        this.loadData();
      },
      methods: {

        complete (index) {
          this.list[index] = !this.list[index]
        },

        async loadData() {

                NProgress.start();
                // try {

                    const response = await axios.get('xperf/AcceuilColab');
                    var res = response.data.data_T_colab;
                    
                    console.log(res);

                    res.forEach(element => {

                      let crm = {
                        nom : element.name,
                        prenom: element.surname,
                        email: element.email,
                        contact: element.phone,
                        ville: element.ville,
                      };

                      this.items.push(crm);
                      
                    });

                // } 
                // catch (error) {
                //         this.$notify({
